import React from 'react';
import MaterialTable from 'material-table';
import {connect} from "react-redux";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../Images/IconPack";

import InputLabel from "@material-ui/core/InputLabel/InputLabel";

import TextField from "@material-ui/core/TextField/TextField";
import { FormattedMessage } from 'react-intl';



const PersonalActivity = (props) => {
    const [searchValue, setSearchValue] = React.useState('');
    const [dateReverse, setDateReverse] = React.useState(false);

    const getDayRow = (type) => {
        let date = new Date();
        var month = ["Jan", "Feb", "Mar", "Apr", "May", "Jun",
            "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"][date.getMonth()];
        let day;
        switch (type) {
            case 'Today':
                day = date.getDate();
                break;
            case 'Yesterday':
                day = date.getDate() - 1;
                break;
            default:
                break;
        }

        return (<TableRow className="date-row">
            <TableCell className="padding-sides-20 color-white" colspan="4">
                <b className="color-white">{type}</b>
                <span className="color-grey inline">{day} {month}</span>
            </TableCell>
        </TableRow>);
    }


    const getTbodyData = (reversed) => {

        if(!reversed){
            return (<>
                {getDayRow('Today')}

                {props.personalActivityData.map((i, key) => {
                    return  ( <TableRow >
                        <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                        <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                        <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
                        <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
                    </TableRow>);
                })}

                {getDayRow('Yesterday')}

                {props.personalActivityData.map((i, key) => {
                    return  ( <TableRow >
                        <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                        <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                        <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
                        <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
                    </TableRow>);
                })}

            </>);
        }else {
            return (<>

                {getDayRow('Yesterday')}

                {props.personalActivityData.map((i, key) => {
                    return (<TableRow>
                        <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                        <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                        <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span>
                        </TableCell>
                        <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
                    </TableRow>);
                }).reverse()}


                {getDayRow('Today')}

                {props.personalActivityData.map((i, key) => {
                    return (<TableRow>
                        <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
                        <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
                        <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span>
                        </TableCell>
                        <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
                    </TableRow>);
                }).reverse()}

            </>);
        }
    };



    let searchedData = props.personalActivityData.filter(i => {
        if (i.user.toLowerCase().includes(searchValue.toLowerCase()) ||
            i.description[0].toLowerCase().includes(searchValue.toLowerCase())||
            i.description[1].toLowerCase().includes(searchValue.toLowerCase())||
            i.action.toLowerCase().includes(searchValue.toLowerCase())) {
            return i
        }
    })

    searchedData = searchedData.map((i, key) => {

        return  ( <TableRow key={key} >
            <TableCell className="padding-sides-15 color-white"><span className="icon-inside align-items-baseline inline">{IconPack.userIcons.forTable}{i.user}</span></TableCell>
            <TableCell className="padding-sides-0 color-white">{i.action}</TableCell>
            <TableCell className="padding-sides-25 color-white">{i.description[0]}<span className="color-grey icon-inside align-items-baseline inline ml-10">{IconPack.licenseIcons.mail}{i.description[1]}</span> </TableCell>
            <TableCell className="padding-sides-20 color-grey">13 Feb, 11:10:22</TableCell>
        </TableRow>);
    });




    return (
        <div className="сustomStyle">
            <div className="filterPanel">
                <FormattedMessage  id='filterPanel.search'>
                    {(msg) => ( <TextField
                        variant="filled"
                        fullWidth
                        className="search_area"
                        placeholder={msg}
                        value={searchValue}
                        onChange={(e)=>{setSearchValue(e.target.value)}}
                    >
                    </TextField>)}
                </FormattedMessage>

                <div className="search_additional_components">
                    <div className="calendar">{IconPack.searchComponents.calendar}</div>
                    <div>{IconPack.searchComponents.filter}</div>
                </div>
            </div>
            <Table className="configTable">
                <TableHead>
                    <TableRow>
                        <TableCell className="padding-sides-20 w160"><FormattedMessage id='table.user' /></TableCell>
                        <TableCell className="padding-sides-0 w140"><FormattedMessage id='table.action' /></TableCell>
                        <TableCell className="padding-sides-25"><FormattedMessage id='table.description' /></TableCell>
                        <TableCell className={dateReverse ? 'padding-sides-25 w140 td-date' : 'padding-sides-25 w140 td-date rotate-child-180 '} onClick={()=>{setDateReverse(!dateReverse)}}>{IconPack.tableIcons.date} <FormattedMessage id='table.date' /></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody className="no-hover">
                    {searchValue.length ? searchedData : getTbodyData(dateReverse) }
                </TableBody>
            </Table>
        </div>
    );
}

const mapStateToProps = (state) => ({
    personalActivityData: state.personalLicenseActivity.activity,
});


export default connect(mapStateToProps)(PersonalActivity);
